.wdu-container {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
}
.wdu-aside {
  position: relative;
  left: 0;
  top: 0;
}
.wdu-main {
  position: relative;
}
.wdu-footer {
  width: 100%;
  left: 0;
  bottom: 0;
  flex-grow: 1;
}
.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.row-col_center {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}
.center {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
}
.row-around {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
}
.row-end {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
}
.row-start {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
}
.col-row_center {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
}
.w-box {
  box-sizing: border-box;
}
.hide-scroll {
  scrollbar-width: none;
}
.hide-scroll::-webkit-scrollbar {
  display: none;
}
.w-col-1 {
  width: 8.33%;
}
.w-col-2 {
  width: 16.66%;
}
.w-col-3 {
  width: 25%;
}
.w-col-4 {
  width: 33.33%;
}
.w-col-5 {
  width: 41.66%;
}
.w-col-6 {
  width: 50%;
}
.w-col-7 {
  width: 58.33%;
}
.w-col-8 {
  width: 66.66%;
}
.w-col-9 {
  width: 75%;
}
.w-col-10 {
  width: 83.33%;
}
.w-col-11 {
  width: 91.66%;
}
.w-col-12 {
  width: 100%;
}
@keyframes modal-open {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes modal-hide {
  from {
    transform: scale(1);
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: scale(0);
  }
}
@keyframes slide-in-left {
  from {
    right: -100%;
    opacity: 0;
  }
  to {
    right: 10px;
    opacity: 1;
  }
}
@keyframes slide-out-left {
  from {
    right: 10px;
    opacity: 1;
  }
  to {
    right: -110%;
    opacity: 0;
  }
}
.cursor-on {
  cursor: pointer;
}
@keyframes height-animate {
  from {
    height: 0px;
  }
  to {
    height: 100px;
  }
}
.w-transition-quick {
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.w-transition-normal {
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.w-transition-slow {
  transition: all 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.large-text {
  font-size: 16px;
  font-weight: 700;
  color: #333;
}
.normal-text {
  font-size: 14px;
  font-weight: 600;
  color: #666;
}
.small-text {
  font-size: 12px;
  font-weight: 700;
  color: #666;
}
.title-text {
  font-size: 16px;
  font-weight: bold;
  color: #333;
  margin: 0 8px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}
.info-text {
  font-size: 14px;
  color: #666;
  margin: 0 8px;
  overflow: hidden;
}
.none-shadow {
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
}
.out-shadow-light {
  box-shadow: 0 3px 20px -6px rgba(0, 0, 0, 0.1);
}
.out-shadow-normal {
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
}
.out-shadow-bold {
  box-shadow: 0 0 8px 4px rgba(0, 0, 0, 0.3);
}
.inside-shadow-light {
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.2);
}
.inside-shadow-medium {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.6);
}
.inside-shadow-bold {
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.6);
}
/* 字体颜色 */
/*通用背景色 */
.w-bgcolor-normal {
  color: #333;
  background-color: white;
}
/* 按钮背景色 */
/* 线条颜色 */
/* 通用背景图像 */
.w-bg-normal {
  background-image: linear-gradient(#ffffff, #f1f1f1);
}
.w-bg-active {
  background-image: linear-gradient(#acdb5f, #8db44f);
}
.w-bg-important {
  background-image: linear-gradient(#6383c6, #4262af);
}
.w-bg-danger {
  background-image: linear-gradient(#CE554E, #B8352E);
}
.w-bg-light {
  background-image: linear-gradient(#fff893, #c9c76e);
}
.w-bg-disabled {
  background-image: linear-gradient(#2B2B2B, #141414);
}
.w-radiu-small {
  border-radius: 5px;
}
.w-radiu-normal {
  border-radius: 8px;
}
.w-radiu-medium {
  border-radius: 10px;
}
.w-radiu-large {
  border-radius: 16px;
}
.w-border-solid {
  border: 1px solid #dedede;
}
.w-border-circle {
  border-radius: 50%;
}
.w-border-light {
  border: 1px solid rgba(207, 207, 207, 0.6);
}
